import $ from "jquery"
import { gsap, TweenMax, TimelineMax, Power0, Power1, Circ } from "gsap";
import ScrollMagic from "scrollmagic"
import 'scrollmagic/scrollmagic/uncompressed/plugins/debug.addIndicators';
import { ScrollMagicPluginGsap } from "scrollmagic-plugin-gsap";
ScrollMagicPluginGsap(ScrollMagic, TweenMax, TimelineMax);


var controller = new ScrollMagic.Controller();
var tween = TweenMax.staggerTo(".fadeIn", 1, { autoAlpha: 1 }, 1);
TweenMax.set(".fadeIn", { autoAlpha: 0 });
TweenMax.set(".whatdev", { opacity: 1, top: 0 });
var tween_text1_fadeout = TweenMax.staggerFromTo(".whatdev", 1, { top: 0, opacity: 1 }, { top: -100, opacity: 0 });
var tween_text2_fadeout = TweenMax.staggerFromTo(".whatdev", 1, { top: 0, opacity: 1 }, { top: -100, opacity: 0 });
var tween_text2_fadein = TweenMax.staggerFromTo(".whatdev", 1, { top: 100, opacity: 0 }, { top: 0, opacity: 1 });
var tween_text3_fadein = TweenMax.staggerFromTo(".whatdev", 1, { top: 100, opacity: 0 }, { top: 0, opacity: 1 });

$(function() { // wait for document ready
    // build scene
    var pin = new ScrollMagic.Scene({
            offset: 0,
            triggerHook: 0.45,
            duration: 1000
        })
        .setPin("#pin1")
        .addIndicators() // add indicators (requires plugin)
        .addTo(controller);
    var fade_image = new ScrollMagic.Scene({
            offset: 200,
            duration: 400
        })
        .setTween(tween)
        .addIndicators()
        .addTo(controller);


    var text1_fadeout = new ScrollMagic.Scene({
            offset: 200,
            duration: 200
        })
        .setTween(tween_text1_fadeout)
        .addIndicators()
        .addTo(controller)
        .on("end", function(event) {
            if (event.scrollDirection == "FORWARD") {
                $(".whatdev").text("파워풀한")
            }
        })
        .on("enter", function(event) {
            if (event.scrollDirection == "REVERSE")
                $(".whatdev").text("열정적인")
        });
    var text2_fadein = new ScrollMagic.Scene({
            offset: 400,
            duration: 200
        })
        .setTween(tween_text2_fadein)
        .addIndicators()
        .addTo(controller);
    var text2_fadeout = new ScrollMagic.Scene({
            offset: 600,
            duration: 200
        })
        .setTween(tween_text2_fadeout)
        .addIndicators()
        .addTo(controller)
        .on("end", function(event) {
            if (event.scrollDirection == "FORWARD") {
                $(".whatdev").text("발전하는")
            }
        })
        .on("enter", function(event) {
            if (event.scrollDirection == "REVERSE")
                $(".whatdev").text("파워풀한")
        });
    var text3_fadein = new ScrollMagic.Scene({
            offset: 800,
            duration: 200
        })
        .setTween(tween_text3_fadein)
        .addIndicators()
        .addTo(controller);

    TweenMax.set(".whatdev", { opacity: 1, top: 0 });
    TweenMax.set(".fadeIn", { autoAlpha: 0 });
});